"use server";

import { SearchParams } from "next/dist/server/request/search-params";

const nextUrl = `${process.env.NEXT_PUBLIC_APP_URL}/api/meals`;

let currCategory: string | null = null;

export async function getMeals(searchParams: Promise<SearchParams>) {
  const queryString = new URLSearchParams(
    (await searchParams) as Record<string, string>
  );

  const response = await fetch(`${nextUrl}?${queryString}`, {
    cache: "no-store",
  });

  if (!response.ok) {
    throw new Error(
      `Failed to fetch meals: ${response.status} ${response.statusText}`
    );
  }

  return response.json();
}

export async function getMealById(id: string) {
  const response = await fetch(`${nextUrl}/${id}`, {
    cache: "no-store",
  });

  if (!response.ok) {
    throw new Error(
      `Failed to fetch meal: ${response.status} ${response.statusText}`
    );
  }

  const meal = await response.json();
  currCategory = meal.strCategory;

  return meal;
}

export async function getMealsByCategory() {
  const response = await fetch(`${nextUrl}?category=${currCategory}`, {
    cache: "no-store",
  });

  if (!response.ok) {
    throw new Error(
      `Failed to fetch meals: ${response.status} ${response.statusText}`
    );
  }

  return response.json();
}
