import Image from "next/image";
import Link from "next/link";
import { Suspense } from "react";
import { getMealById, getMealsByCategory } from "@/app/actions/meals";
import MealIngredients from "@/components/MealIngredients";
import CategoryMeals from "@/components/CategoryMeals";

export default function MealPage({ params }: { params: { id: string } }) {
  const meal = getMealById(params.id);
  const categoryMeals = getMealsByCategory(meal.strCategory);

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="flex flex-col lg:flex-row gap-8">
        <div className="lg:w-2/3">
          <div className="flex flex-col md:flex-row gap-8 mb-8">
            <Image
              src={meal.strMealThumb}
              alt={meal.strMeal}
              className="w-full md:w-1/2 h-96 object-cover rounded-lg"
              width={500}
              height={500}
              priority
            />
            <div className="md:w-1/2">
              <h1 className="text-3xl font-bold mb-4">{meal.strMeal}</h1>
              <Link
                href={`/?country=${meal.strArea}`}
                className="text-blue-600 hover:underline mb-4 block"
              >
                {meal.strArea}
              </Link>
              <div className="prose max-w-none">
                <h2 className="text-xl font-semibold mb-2">Instructions</h2>
                <p className="whitespace-pre-line">{meal.strInstructions}</p>
              </div>
            </div>
          </div>

          <Suspense fallback={<div>Loading ingredients...</div>}>
            <MealIngredients meal={meal} />
          </Suspense>
        </div>

        <Suspense fallback={<div>Loading similar meals...</div>}>
          <CategoryMeals category={meal.strCategory} meals={categoryMeals} />
        </Suspense>
      </div>
    </div>
  );
}
